$small: 320px;
$medium: 768px;
$desktop: 1200px;

@mixin responsive($media) {
	@if $media == small {
		@media only screen and (max-width: $small - 1) // -599
			{@content}
	}
	@if $media == mobile {
		@media only screen and (max-width: $medium - 1) // -767
			{@content}
		}
	@else if $media == tablet {
		@media only screen and (min-width: $medium) and (max-width: $desktop) // 768-1024
			{@content}
	}
	@else if $media == tabletdesktop {
		@media only screen and (min-width: $medium) // +768
			{@content}
	}
// 	@else if $media == device {
// 		@media only screen and (max-width: $desktop) // -1024
// 			{@content}
// 	}
	@else if $media == desktop {
		@media only screen and (min-width: $desktop ) // +1024
			{@content}
	}
}
@mixin gradient-x ($start-color: $gray-700, $end-color: $gray-800, $start-percent: 0%, $end-percent: 100%) {
	background-image: ms-linear-gradient(to right, $start-color $start-percent, $end-color $end-percent);
	background-image: linear-gradient(to right, $start-color $start-percent, $end-color $end-percent);
	background-repeat: repeat-x;
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr=$start-color, endColorstr=$end-color, GradientType=1);
}