@import "../libs/bootstrap/mixins/_gradients.scss";
@import "../templates/variables";
@import "../templates/mixins";
@import "../templates/forms";

.main-info {
	background: url("../images/header-bgr.jpg") 350px 0 no-repeat;
	background-size: 100% auto;
	position: relative;
	min-height: 513px;
	margin-bottom: 40px;

	.logo__main-info {
		width: 220px;
		height: 91px;
		background-image: url('../images/logo.png');
		margin-top: 25px;
	}

	h1 {
		padding-right: 500px;
		margin-top: 12px;
		margin-bottom: 16px;
	}

	&-list {
		margin-left:0;
		padding-right: 500px;
		padding-left:0;

		&__element {
			color:  #515254;
			font-size: 20px;/* Приближение из-за подстановки шрифтов */
			font-weight: 400;
			list-style-type: none;
			line-height: 65px;

			@include responsive(tabletdesktop) {
				font-size: 30px;
				line-height: 150%;
			}

			&:before {
				content: "";
				display: inline-block;
				width: 20px;
				height: 20px;
				background: url('../images/icons/check-circle-regular.svg') top left no-repeat;
				margin-right: 10px;
			}
		}
	}
	.btn {
		position:absolute;
		bottom: 30px;
		left: 50%;
		min-width: 265px;
		margin-left: -28px;
	}
}

.konkurs {
	padding-top: 20px;
	padding-bottom: 60px;

	.btn {
		min-width: 470px;
		margin: 60px auto 20px;
		display: block;
		padding: 18px;
		font-size: 23px;
		letter-spacing: 0.4px;
		font-weight: 700;
		}
	h2 {
		margin-bottom:20px;
		margin-top: 20px;
	}
}
.bonus {
	@include gradient-x(#cdd8de, #add6ec);
	padding: 55px 0 45px;

	&-list {
		display: flex;
		justify-content: space-between;
		padding: 0;

		&__element {
			color: $primary;
			display: block;
			text-align: center;
			//flex: 25% 0 0;
			padding: 0 10px;

			&__img {
				@include gradient-directional(#48c3f0, #005d8c);
				width: 220px;
				height:220px;
				border-radius: 110px;
				line-height: 210px;
				margin: 0 auto 28px;

				&:before {
					content: "";
					width:50%;
					height: 50%;
					line-height: initial;
					vertical-align: middle;
					display: inline-block;
					background-size: cover;
					background-repeat: no-repeat;
					background-position: center center;
				}
			}
			@for $i from 1 through 4 {
				&:nth-child(#{$i}) {
					.bonus-list__element__img {
						&:before {
							background-image: url('../images/icons/bonus'+$i+'.svg');
						}
					}
				}
			} 
			&__text {
				font-weight: 600;
				font-size: 25px;
				line-height: 118%;
			}
		}
	}
}
.conditions {
	position: relative;
	height:463px;
	padding-top: 80px;
	
	@include responsive(desktop) {
		padding-top: 40px;
	}

	.container {
		position: relative;
    	height: 100%;
    	margin-bottom: -40px;
	}

	&:before {
		content: "";
		display: block;
		position: absolute;
		z-index: 2;
		width: 600px;
		height: 100%;
		top:0;
		left:0;
		background: url("../images/condition-bgr.png") -70px -1px no-repeat;
		background-size: 100% auto;
	}
	h2 {
		text-align: right;
		margin-top: 0;
		margin-bottom: 30px;
	}
	.comment {
		@include gradient-x(#cdd8de, #add6ec);
		max-width: 745px;
		width: 100%;
		margin-right: 0;
		margin-left: auto;
		color: $primary;
		text-align: right;
		position: absolute;
		bottom: 0;
		right: 0;
		font-size: 20px;
		font-weight: 600;
		padding: 16px 15px 16px 100px;
		overflow: hidden;
		height: 60px;
		z-index:-1;

		@include responsive(desktop) {
			max-width: 845px;
			height: 100px;
		}

		@media (min-width: 1300px) {
			max-width: 80%;
		}

		&:before {
			content:"";
			width: 73px;
			height: 100%;
			position: absolute;
			top:-2px;
			left:-2px;
			z-index:1;
//			background: url('../images/triangle.png') 0 0 no-repeat;
 			border: 60px solid transparent;
 			border-left: 100px solid white;
 			border-top: 100px solid white;
		}

		a {
			color: inherit;
			text-decoration: none;

			&:hover,
			a:active {
				text-decoration: underline;
			}
		}
	}
	.comment-inner {
		font-size: 14px;
		color: $primary;
		max-width: 600px;
		position: absolute;
		right: 0;
		bottom: -25px;
		padding-right: 15px;
		text-align: right;

		@include responsive (desktop) {
			font-size: 20px;
		}

		a {
			color: inherit;
			text-decoration: none;

			&:hover,
			a:active {
				text-decoration: underline;
			}
		}
	}
	.nav-tabs_conditions {
		border-bottom: none;
// 		justify-content: stretch;
// 		flex-wrap: nowrap;
		display:block;
		text-align: right;
		padding-left: 450px;

		&__item {
			display:inline-block;
			//width: auto;
			margin: 0;

			&__link {
				text-decoration: none;
				border: none;
				border-bottom: 6px solid $text-color;
				background: none;
				display:inline-block;
				font-size: 20px;
		
				@include responsive(desktop) {
					font-size:23px;	
				}

				&.active {
					color: $secondary;
					border-bottom: 6px solid $secondary;
					background: none;
				}
			}
			&:first-child {
				.nav-tabs_conditions__item__link {
					padding: 8px 8px 8px 0;
					margin-right: -6px;
				}
			}
			&:last-child {
				.nav-tabs_conditions__item__link {
					padding: 8px 0 8px 8px;
				}
			}
		}
	}
	.tab-content {
		margin-top: 15px;
		text-align: right;
		line-height: 130%;
		padding-left: 450px;
		font-size: 14px;

		@include responsive(desktop) {
			font-size:23px;	 
		}
	}
}
.stages {
	padding-top:10px;
	padding-bottom: 32px;
	@include gradient-x(#cdd8de, #add6ec);

	h2 {
		margin-bottom:40px;
	}

	&-list {
		padding-left:0;
		display: flex;
		

		&__element {
			display: block;
			position: relative;

			&:before {
				content: "";
				display: block;
				position: absolute;
				height: 20px;
				top: 50px;
				width: 100%;

			}

			&:nth-child(1) {
				width: 50%;

				&:before {
					@include gradient-x(#00aeef, #005e8d);
				}

			}
			&:nth-child(2) {
				width: 25%;

				&:before {
					@include gradient-x(#f99c1c, #f15922);
				}

				.stages-list__element__period-start {
					color: $secondary;
					transform: translateX(-50%);
					display: inline-block;

				}
			}
			&:nth-child(3) {
				width: 25%;

				&:before {
					@include gradient-x(#00aeef, #005e8d);
				}

				.stages-list__element__period-start {
					transform: translateX(-50%);
					display: inline-block;
				}

				.stages-list__element__period-end {
					color: $secondary;
					transform: none;
				}
			}


			&__period-start {
				color:  $primary;
				font-size: 20px;/* Приближение из-за подстановки шрифтов */
				text-transform: uppercase;
				
				@include responsive(desktop) {
					font-size: 30px;
				}
			}
			&__period-end {
				@extend .stages-list__element__period-start;
				float: right;

				@include responsive(desktop) {
					float: none;
				}

			}
			&__description {
				max-width: 240px;
				margin: 70px auto 0;
				font-size:15px;
				padding: 0 10px;

				@include responsive(desktop) {
					font-size: 21px;
				}

			}

		}
	}
}
.nominations {
	padding-top: 0;
	padding-bottom: 0;
	
	&-list {
// 		display: flex;
// 		flex-wrap: wrap;
		justify-content: space-between;
		padding-left:0;
		margin-bottom:0;

		&__element {
			text-align: center;
			display: block;
			flex: 310px 0 0;
			min-width: 310px;
			//background-size: 275px auto;
			//background-repeat: no-repeat;
			//background-position: top center;
			//padding-top: 293px;
			margin-bottom: 45px;
			

			&:hover {
				.nominations-list__element__bgr {
					&:before{
						transform: scale(1.1);
					}
				}
				
			}

			&__bgr {
				width: 275px;
				height: 275px;
				overflow: hidden;
				margin-bottom: 20px;

				&:before {
					content:"";
					display: block;
					margin:auto;
					width: 275px;
					height: 275px;
					background-size: contain;
					background-repeat: no-repeat;
					background-position: top center;
					transition: transform 0.5s;	
				}
				
			}
			
			@for $i from 1 through 6 {
				&:nth-child(#{$i})	{
					.nominations-list__element__bgr {
						&:before {
							background-image: url('../images/nomination'+$i+'.jpg');
						}
					}
				}
			}
			

			&__header {
				color: $secondary;
				text-transform: uppercase;
				font-size: 25px;
				margin-bottom: 3px;
				line-height: 100%;
			}
			&__description {
				font-size: 18px;
				color: #58595b;
				line-height: 130%;
			}
			&__link {
				display: block;
				text-decoration: none;
			}
		}
	}
}
.jury {
	//display: none;
	background: url('../images/jury-bgr.jpg') top center no-repeat;
	background-size: cover;

	h2 {
		margin-top:10px;
		margin-bottom: 50px;
	}

	&-list {
		display: flex;
		justify-content: space-around;
		padding-left: 0;
		margin-bottom: 0;

		&__element {
			display: block;
			text-align: center;
			flex: 33% 0 0;


			&__img {
				width: 250px;
				height: 250px;
				border-radius: 125px;
			}
			&__name {
				font-weight: 600;
				margin: 20px auto;
				max-width: 250px;
				line-height: 130%;
				min-height: 60px;
			}
			&__position {
				font-size: 15px;
				font-weight: 300;
				max-width: 250px;
				margin: 34px auto 0;
				line-height: 130%;
			}
		}

	}
}
