@import "../templates/variables";

.form {
    h2 {
        color:  #ff7200;
        font-size: 48px;/* Приближение из-за подстановки шрифтов */
        font-weight: 300;
    }
    &-group {
        margin-bottom: 30px;

        &.required {
            label {
                margin-bottom: 12px;

                &:after {
                    content:"*";
                    color: $red;
                    display: inline-block;
                    margin-left: 10px;
                }
            }
        }
        &__date {
            .date-control {
                padding-right: 50px;
            }
            .fas {
                position: absolute;
                width: 23px;
                height: 23px;
                font-size: 23px;
                bottom: 25px;
                right: 30px;
                color: $secondary;
            }
        }
    }

    &-control {
        height: 70px;
        border-color: $border-color-primary;
        border-width: 2px;
        outline-color: transparent;
        font-size: 23px;

        &:hover,
        &:active {
            border-color: $border-color-primary-hover;
        } 
        &:invalid {
            border-color: $error;
        }
    }

    .date-control {
        padding-right: 30px;

    }
    
    .choose-file {
        &.form-group {
            position: relative;
            margin-bottom: 20px;
        }
        .form-control {
            visibility: hidden;
            width: 100%;
            height: 100%;
            z-index: 1;
            position: absolute;
            top: 0;
            left: 0;

            &-wrapper {
                background-color: $border-color-primary;
                height: 70px;
                border-radius: 4px;
                transition: background-color 0.5s, color 0.5s;

                label {
                    width: 100%;
                    height: 100%;
                    padding: 15px 30px;
                    display: flex;
                    justify-content: stretch;
                    align-items: center;
                    color: #4a778f;

                    .fas {
                        margin: auto 0 auto auto;
                        flex: 20px 0 0;
                    }
                }

                &:hover,
                &:active {
                   background-color: $border-color-primary-hover;

                   label {
                       color: white;
                   }
                }
            }
        }
    }
    .small {
        font-size: 18px;
        width: 100%;
        margin-bottom:30px;;

    }
    .btn {
        height: 70px;
        padding: 15px 30px;
        text-transform: initial;
        font-size: 24px;
        letter-spacing: initial; 
        
        &[type="submit"] {
            min-width: 50%;
        }
    }
   
    .custom-checkbox {
        padding-left: 3rem;
        font-size: 18px;
        color: $text-color;
        
        .custom-control-label {
            
            &:before {
                background-color: $secondary;
                width: 22px;
                height: 22px;
                left: -3rem;
            }
            &:after {
                top: 0rem;
                left: -3.3rem;
                width: 2rem;
                height: 2rem;
            }
        }
        .custom-control-input:checked ~ .custom-control-label::before {
           background-color: $secondary;
        }
    }
    .label-required {
        color: $red;
        margin-right: 10px;
    }
    .selectize {
        &-control {
            &.select {
                .selectize-input {
                    &:after { 
                        border-radius:2px;
                        border-color: $secondary  transparent transparent transparent;
                        border-width: 10px 10px 0 10px;
                    }
                    &.input-active {
                        &:after {
                            border-color: transparent transparent $secondary transparent;
                            border-width: 0 10px 10px 10px;
                        }
                    }
                }
            }
            .selectize-dropdown {
                border: 2px solid $border-color-primary;
                border-top: none;
                height: auto;
                max-height: 500px;
               

                &-content {
                    .option {
                        padding: 20px;
                        font-size: 23px;

                        &:hover,
                        &:active {
                            background-color: $border-color-primary-hover;
                            color: white;
                        }
                    }
                }
            }
        }
        &-input {
            height: 70px;
            border-color: $border-color-primary;
            border-width: 2px;
            outline-color: transparent;
            font-size: inherit;
            color: $text-color;
            padding: 20px;

            &:hover,
            &:active {
                border-color: $border-color-primary-hover;
            } 
            &.error {
                border-color: $error;
            }
        }
       
    }
}